@import url(https://fonts.googleapis.com/css2?family=Work+Sans:wght@200;800&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.loading {
  align-items: center;
  display: flex;
  justify-content: center;
}

.logo {
  width: -webkit-fit-content;
  width: -moz-fit-content;
  width: fit-content;
}

.white-logo {
  color: white;
}

.trybe {
  font-family: Work Sans , sans-serif;
  font-size: 1.5em;
  font-weight: 800;
  margin: 0;
}

.tunes {
  flex-grow: 2;
  font-family: Work Sans , sans-serif;
  font-size: 1.8em;
  font-weight: 200;
  margin: 0;
}

.headphones {
  color: #0c9;
  font-size: 2em;
}

.top-header-box {
  background-color: #033;
  padding: 0.5em 2em;
}

.bot-header-box {
  margin: 0;
  padding: 0;
}

.bot-header-box li {
  flex-grow: 1;
  font-size: 1.1em;
  font-weight: 600;
  list-style: none;
  padding: 1em;
  text-align: center;
}

.tab a {
  color: #0c9;
}

.tab-on a {
  color: white;
}

.bot-header-box li a {
  text-decoration: none;
}

.tab {
  background-color: #f0f0f0;
  color: #0c9;
}

.tab-on {
  background-color: #099;
  color: white;
}

.user-box {
  background-color: white;
  border-radius: 20px;
  height: -webkit-fit-content;
  height: -moz-fit-content;
  height: fit-content;
  padding: 0.2em;
}

.user-name {
  color: #033;
  margin: 0 1.5em;
}

.user-circle {
  color: #0c9;
  font-size: 1.8em;
}

.login-box {
  background-color: #f0f0f0;
  height: 100vh;
  justify-content: center;
}

.login-loading {
  font-size: 5vw;
}

.login-form-box {
  background-color: white;
  border-radius: 10px;
  box-shadow: 4px 4px 4px rgb(123, 123, 123);
  margin-top: 2em;
  padding: 3em;
}

.login-form-box input {
  margin-bottom: 1em;
  padding: 0.2em;
}

.login-form-box button {
  background-color: #00c;
  border: 2px solid #00c;
  border-radius: 3px;
  color: white;
  padding: 0.2em;
}

.login-form-box .button-disabled {
  background-color: rgb(122, 122, 122);
  border: 2px solid rgb(122, 122, 122);
}

.search-box button {
  background-color: #00c;
  border: 2px solid #00c;
  border-radius: 3px;
  color: white;
  padding: 0.4em;
}

.search-box input {
  margin-right: 1em;
  padding: 0.4em;
  width: 40%;
}

.search-box {
  margin: 2em auto;
}

.container-column {
  display: flex;
  flex-direction: column;
}

.container-row {
  display: flex;
}

.ai-end {
  align-items: flex-end;
}

.ai-center {
  align-items: center;
}

.ai-stretch {
  align-items: stretch;
}

.jc-center {
  justify-content: center;
}

.jc-spacebetween {
  justify-content: space-between;
}

.jc-evenly {
  justify-content: space-evenly;
}

.center-text {
  color: #033;
  font-size: 3em;
  margin: 3em auto;
  text-align: center;
}

